.nb-scroll {
  position: relative;
  width: 100%;
  height: 100%;
}
.nb-scroll .item-list {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
  will-change: transform;
  contain: layout;
}
.nb-scroll .item-list .item {
  position: absolute;
  width: 100%;
}
.nb-scroll .item-list .bottom {
  position: absolute;
  width: 100%;
  padding: 0px 0px;
  text-align: center;
  color: #aaa;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100px;
}
.nb-scroll .flash-list {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
  will-change: transform;
  contain: layout;
  top: -100000px;
  position: absolute;
  visibility: hidden;
  z-index: -1000;
}
.nb-scroll .model {
  top: -1000px;
  visibility: hidden;
  position: absolute;
  width: 100%;
}
.nb-scroll .tombstone {
  border-bottom: 1px solid #eee;
  position: absolute;
  width: 100%;
  will-change: transform;
  contain: layout;
}
.nb-scroll .runway {
  width: 1px;
  height: 1px;
  position: absolute;
  will-change: transform;
  contain: layout;
}
